.File_Added {
  position: relative;
  background-color: #f2f3f7;
  border-color: #b8bac2;
}

.File_Added .Upload_Overlay {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 11;
}

.File_Added .Upload_Overlay {
  background-color: #2196f3;
  color: #fff;
  padding: 6px 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 4px;
  -webkit-transition: 0.1s background ease-in;
  transition: 0.1s background ease-in;
}

.File_Added .Upload_Overlay input {
  cursor: pointer;
}

.File_Added .image_preview {
  height: 244px;
  -o-object-fit: contain;
  object-fit: contain;
}

.PreFile .input-text h1 {
  font-size: 16px;
}

.PreFile .input-text p {
  font-size: 12px;
}

