.Container {
  .CustomButton {
    border: 1px solid #dee2ea;
    box-sizing: border-box;
    border-radius: 8px;

    background: #fff !important;

    color: #32c5ff;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    text-transform: capitalize;

    min-height: 40px;
    height: fit-content;
  }
}

.Wrapper {
  h1 {
    font-weight: bold;
  }

  .CustomButton {
    border: 1px solid #dee2ea;
    box-sizing: border-box;
    border-radius: 8px;

    color: #32c5ff;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-align: center;

    min-height: 40px;
    height: fit-content;
  }

  .SaveButton {
    background: #12b0ee !important;
    color: #fff;
  }
}
