.ItemCard {
  background: #ffffff;
  box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.2);
  border-radius: 16px !important;
  margin: 7% auto;
}
.ItemContent {
  padding-bottom: 0 !important;

  & .ItemCard {
    margin: auto -16px;
    border: none;
    border-top: 1px solid #0000001f;
    box-shadow: none;
    border-radius: 0 !important;

    &:nth-child(4) {
      margin-top: 16px;
    }

    &:last-child {
      border-bottom: 1px solid #0000001f;
    }
  }
}
.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  h1 {
    font-weight: 500;
    font-size: large;
    margin-left: 5px;
  }

  .Card_Control {
    display: flex;
    align-items: center;

    button {
      box-shadow: unset !important;
      padding: 4px;
    }

    .selectEmpty {
      height: 10px;
      div:first-child {
        display: flex;
        align-items: center;

        justify-content: center !important;
      }
    }
  }
}

.Lead_Color {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-right: 6px;
}

.MenuList {
  border-radius: 10px;

  display: flex !important;
}
