.Drawer {
  position: relative;

  width: 85% !important;
  margin: 10px auto;

  .DrawerActions {
    button {
      width: 46%;
      justify-content: center;

      border-radius: 8px;

      color: #fff;
    }

    .Reset {
      background-color: #bebebe !important;
    }

    .Save {
      background-color: #12b0ee !important;
    }
  }
}

.Header {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;

  font-weight: 500;
  font-size: 22px;
}
