.Wrapper {
  background-color: #fff;
  height: 65px;

  position: relative;

  padding: 10px 15px;

  border: 1px solid #f0f0f0;

  display: flex;
  justify-content: space-between;

  img {
    height: 100%;
  }

  .Buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;

    button {
      min-width: 90px !important;
      border-radius: 4px;

      justify-content: center;
    }

    .SaveButton {
      background: #f2f3f7 !important;
    }
  }
}
