.RootListItem {
  border-radius: 8px;
}

.SubList {
  pointer-events: none;

  .ListIcon {
    min-width: 40px;
  }

  .SubListItem {
    transition: 0.1s all ease-out;

    cursor: grab;

    border: 3px solid transparent;

    background: rgba(196, 196, 196, 0.2);
    border-radius: 25px;
    padding: 8px;

    &:hover {
      border: 3px dashed #12b0ee;
      box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.25);
    }

    &:focus {
      border: 3px dashed transparent;
    }
  }

  .SubListItemText {
    color: #000;
  }
}
