.Upload_Overlay {
  position: absolute;

  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);

  z-index: 11;
}

.Upload_Overlay {
  color: #fff;

  padding: 6px 20px;

  width: fit-content;

  border-radius: 4px;

  input {
    cursor: pointer;
  }

  transition: 0.1s background ease-in;
}

.image_preview {
  width: 100%;

  height: 244px;

  object-fit: cover;
}

.File_Preview {
  border: 2px dashed #b8bac2;
  border-radius: 8px;

  height: 120px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  opacity: 0.5;
  width: 100%;

  div:nth-child(1) {
    column-gap: 8px;
    p {
      font-family: Roboto;
      font-size: 16px;
      line-height: 19px;

      color: #000000;
    }

    svg {
      font-size: 40px;
    }
  }
}

.RemoveButton {
  box-sizing: border-box;
  border-radius: 8px;

  width: 72px;
  height: 37px;

  font-weight: 500;
  font-size: 13px;

  justify-content: center;

  color: #ffffff;

  &:hover {
    text-decoration: unset;
  }
}
