$handleRadius: 12px;
$handleSize: 9px;
$handleBorder: 3px solid #6a6a6a;

.BottomHandle {
  &.flow__handle {
    background: white;
    border: $handleBorder;
    width: $handleSize;
    height: $handleSize;
    position: unset;
    transform: unset;
    border-width: 3px !important;
  }
}

.Bottom_handle_IconButton {
  padding: 2px;
}

.TopHandle {
  &.flow__handle {
    background: white;
    border: $handleBorder;
    position: unset !important;
    transform: unset;
    width: $handleSize;
    height: $handleSize;
  }
}
