.ListIcon {
  min-width: 40px;
}

.Container {
  pointer-events: all;
}
.Container .SubListItem {
  transition: 0.1s all ease-out;
  cursor: -webkit-grab;
  cursor: grab;
  border: 3px solid transparent;
  background: rgba(196, 196, 196, 0.2);
  border-radius: 25px;
  padding: 8px;
}
.Container .SubListItem:hover {
  border: 3px dashed #12b0ee;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.25);
}
.Container .SubListItem:focus {
  border: 3px dashed transparent;
}
.Container .SubListItemText {
  color: #000;
} /*# sourceMappingURL=NodeListItem.module.css.map */
