.ItemCard {
  background: #ffffff;
  box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.2);
  border-radius: 16px !important;
  margin: 7% auto;
}

.ItemContent {
  padding-bottom: 0 !important;
}
.ItemContent .ItemCard {
  margin: auto -16px;
  border: none;
  border-top: 1px solid rgba(0, 0, 0, 0.1215686275);
  box-shadow: none;
  border-radius: 0 !important;
}
.ItemContent .ItemCard:nth-child(4) {
  margin-top: 16px;
}
.ItemContent .ItemCard:last-child {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1215686275);
}

.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Header h1 {
  font-weight: 500;
  font-size: large;
  margin-left: 5px;
}
.Header .Card_Control {
  display: flex;
  align-items: center;
}
.Header .Card_Control button {
  box-shadow: unset !important;
  padding: 4px;
}
.Header .Card_Control .selectEmpty {
  height: 10px;
}
.Header .Card_Control .selectEmpty div:first-child {
  display: flex;
  align-items: center;
  justify-content: center !important;
}

.Lead_Color {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-right: 6px;
}

.MenuList {
  border-radius: 10px;
  display: flex !important;
}/*# sourceMappingURL=SectionCard.module.css.map */