.Message_Wrapper {
  .Message_Field {
    position: relative;
    margin-bottom: 1rem;
  }

  .CustomButton {
    border: none;
    background: transparent !important;
    box-sizing: border-box;
    border-radius: 3px;
    color: #32c5ff;
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
    text-align: center;
    height: fit-content;
    padding: 4px;
    position: absolute;
    bottom: 5px;
    left: 10px;

    &:hover {
      background: rgba(25, 118, 210, 0.04) !important;
    }
  }
}
