.Upload_Overlay {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 11;
}

.Upload_Overlay {
  color: #fff;
  padding: 6px 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 4px;
  transition: 0.1s background ease-in;
}
.Upload_Overlay input {
  cursor: pointer;
}

.image_preview {
  width: 100%;
  height: 244px;
  -o-object-fit: cover;
     object-fit: cover;
}

.File_Preview {
  border: 2px dashed #b8bac2;
  border-radius: 8px;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0.5;
  width: 100%;
}
.File_Preview div:nth-child(1) {
  -moz-column-gap: 8px;
       column-gap: 8px;
}
.File_Preview div:nth-child(1) p {
  font-family: Roboto;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}
.File_Preview div:nth-child(1) svg {
  font-size: 40px;
}

.RemoveButton {
  box-sizing: border-box;
  border-radius: 8px;
  width: 72px;
  height: 37px;
  font-weight: 500;
  font-size: 13px;
  justify-content: center;
  color: #ffffff;
}
.RemoveButton:hover {
  text-decoration: unset;
