.File_Added {
  position: relative;
  background-color: #f2f3f7;
  border-color: #b8bac2;

  .Upload_Overlay {
    position: absolute;

    left: 50%;
    top: 50%;

    transform: translate(-50%, -50%);

    z-index: 11;
  }

  .Upload_Overlay {
    background-color: #2196f3;
    color: #fff;

    padding: 6px 20px;

    width: fit-content;

    border-radius: 4px;

    input {
      cursor: pointer;
    }

    transition: 0.1s background ease-in;
  }

  .image_preview {
    height: 244px;

    object-fit: contain;
  }
}

.PreFile {
  .input-text {
    h1 {
      font-size: 16px;
    }

    p {
      font-size: 12px;
    }
  }
}
