.Container {
  .CustomButton {
    border: 1px solid #dee2ea;
    box-sizing: border-box;
    border-radius: 8px;

    background: #fff !important;

    color: #32c5ff;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-align: center;

    min-height: 40px;
    height: fit-content;
  }
}

.box {
  width: 100%;
  height: 110px;

  box-sizing: border-box;
  box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  background: #ffffff;

  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  margin: 7% auto 15px;
  padding-left: 15px;

  h1 {
    font-size: 18px;
    color: #24272c;
  }

  p {
    margin-top: 6px;

    font-size: 15px;
    color: #24272c;
  }

  border: 1px solid #00c02a;
}
