.Search_Box {
  position: absolute;
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  overflow: hidden;
}

.Search_Box .Input_Container {
  width: 100%;
  position: relative;
}

.Search_Box .Input_Container button {
  padding: 3px;
  margin-right: 5px;
  opacity: 0.7;
}

.InputField {
  width: 100%;
  position: relative;
  border-radius: 4px;
  padding: 5px 5px 5px 10px;
}

