.Drawer {
  position: relative;
  width: 85% !important;
  margin: 10px auto;
}

.Drawer .DrawerActions button {
  width: 46%;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  border-radius: 8px;
  color: #fff;
}

.Drawer .DrawerActions .Reset {
  background-color: #bebebe !important;
}

.Drawer .DrawerActions .Save {
  background-color: #12b0ee !important;
}

.Header {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 500;
  font-size: 22px;
}

