$handleRadius: 12px;
$handleSize: 9px;
$handleBorder: 3px solid #6a6a6a;

.CustomNode {
  padding: 11px;

  box-shadow: 0 0 0 1px #e7e7e7;
  border-radius: 25px;

  background: #f3f3f3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;

  max-width: 250px;
  width: 100%;
  min-width: 100px;

  transition: 0.1s ease-in;

  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  text-align: center;
  font-size: 12px;
  color: #24272c;

  &.Active {
    box-shadow: 0 0 0 1px #6a6a6a, inset 0 0 0 1px #6a6a6a;
  }
}

.BottomHandle_Wrapper {
  display: flex;
  justify-content: space-around;
  bottom: 0;
  left: 0;
  transform: translateY(50%);
  position: absolute;
  width: 100%;
}

.TopHandle_Wrapper {
  display: flex;
  justify-content: space-around;
  position: absolute;
  transform: translateY(-50%);
  top: 0;
  left: 0;
  width: 100%;
}

.NodeDataLabel {
  position: absolute;
  z-index: 11;

  color: #fff;

  border-radius: 3px;

  p {
    font-weight: 900;
    font-size: 10px;
    line-height: 1;

    padding: 5px;

    text-align: center;
  }

  &.StartType {
    background: #1bb954;

    top: -10px;
  }

  &.EndType {
    bottom: -8px;
    background: #e02020;
  }
}
